import React,{ useState ,useEffect} from 'react';
import { Form,Button } from 'semantic-ui-react';
import axios from "axios";

export default function App(){
  const [ name , setName ]=useState('');
  const [ age , setAge ]=useState('');
  const [ salary , setSalary ]=useState('');
  const [ workspace , setWorkspace ]=useState('');
  const [APIdata,setAPIdata]=useState([]);

  const onSubmit=()=>{
    axios.post('https://sheet.best/api/sheets/a7ff8505-9620-4130-b302-5416d3bcf54e' ,{name,age,salary,workspace})
  }
  useEffect(()=>{
    axios.get('https://sheet.best/api/sheets/a7ff8505-9620-4130-b302-5416d3bcf54e')
    .then((incomingData)=>{
      setAPIdata(incomingData.data);    
    })
  },[])
    
  return (
    <div><Form style ={{padding:20}}>
    <Form.Field>
      <label>Name</label>
      <input placeholder='Name' 
      onChange={(e)=>setName(e.target.value)}/>
    </Form.Field>
    <Form.Field>
      <label>Age</label>
      <input placeholder='Age' 
      onChange={(e)=>setAge(e.target.value)}/>
    </Form.Field>
    <Form.Field>
      <label>Salary</label>
      <input placeholder='Salary' 
      onChange={(e)=>setSalary(e.target.value)}/>
    </Form.Field>
    <Form.Field>
      <label>Workspace</label>
      <input placeholder='Workspace' 
      onChange={(e)=>setWorkspace(e.target.value)}/>
    </Form.Field>

    <Button type='submit' onClick={onSubmit}>Submit</Button>
  </Form></div>
  )
}
